ext-weight-semibold"},[_vm._v("Förändringstakt utsläpp koldioxid (3 – −2 poäng)")]),_vm._v(" Här värderar vi hur utsläppen av koldioxid har förändrats inom den enskilda kommunen. Det vi jämför är hur utsläppen förändrats mellan de fem senaste mätperioderna. Kommunen med bäst resultat (störst utsläppsminskningstakt uttryckt i procent) får 3 poäng sedan i fallande skala. De kommuner som har ökat sina utsläpp får upp till 2 minuspoäng. I detta nyckeltal inkluderas industrin för att påvisa ökningar eller minskningar. ")])]),_c('h4',[_vm._v("Industrin utsläpp – exkluderas i två av fyra nyckeltal")]),_c('p',[_vm._v("Industrins utsläpp räknas inte med i nyckeltal 1 och 2 som avser kommunens totalutsläpp per capita. Det beror på att dessa två nyckeltal jämför de olika kommunerna med varandra. En jämförelse som inkluderar industrins utsläpp upplevs här som orättvis eller missvisande eftersom kommunen har liten rådighet över dessa utsläpp, samtidigt som de kan komma att ha ett mycket stort genomslag på de sammanlagda utsläppen i den enskilda kommunen. Industrins utsläpp regleras också på överstatlig nivå idag (EUs system för handel med utsläppsrätter, EU-ETS) och kan betraktas som en nationell, snarare än lokal, angelägenhet. ")]),_c('p',[_vm._v("I de två nyckeltal som avser den egna förändringstakten (nyckeltal 3 och 4) sker inte en jämförelse mellan kommunerna. Istället jämför vi kommunen med sig själv (hur stora utsläppsförändringar har skett över fem år inom kommunen). Här påverkar inte fördelningen av större industrtianläggningar över landet eller dessas inverkan på kommunens totalutsläpp, poängen. Därför låter vi industrins utsläpp ingå i dessa två nyckeltal.")]),_c('h4',[_vm._v("Utrikes transporter – ingår ej")]),_c('p',[_vm._v("Utrikes transporter är en sektor i den nationella emissionsdatabasen. Vi har exkluderat denna sektor från samtliga fyra nyckeltal i enlighet med Naturvårdsverkets och SMEDs rekommendationer. I Metod- och kvalitetsbeskrivning för geografiskt fördelade emissioner till luft (submission 2019) står bland annat:")]),_c('p',[_vm._v("”För utsläpp av växthusgaser från denna sektor (utrikes transporter, vår anm.) rekommenderas att dessa endast studeras på nationell nivå.”")]),_c('p',[_vm._v("För mer information om dessa rekommendationer "),_c('a',{attrs:{"href":"https://naturvardsverket.diva-portal.org/smash/get/diva2:1330910/FULLTEXT01.pdf"}},[_vm._v("se sid. 98 i Metod- och kvalitetsbeskrivning för geografiskt fördelade emissioner till luft (submission 2019).")])]),_c('h5',[_vm._v("Nyckeltal 1 – Utsläpp växthusgaser totalt (0 – 2 poäng)")]),_c('p',[_vm._v("Här bedöms de genomsnittliga utsläppen av koldioxidekvivalenter de senaste fem mätperioderna – per invånare (per capita). Per capita-uträkningen har skett för varje år separat och med hjälp av kommunal befolkningsstatistik från SCB.")]),_c('p',[_vm._v("Kommunen med lägst utsläpp per person får här 2 poäng. Kommun med utsläpp om 25 ton per person eller mer får 0 poäng. Utsläpp på över 25 ton per person och år räknas alltså som 25 ton. Denna gräns är införd eftersom det finns en liten grupp extremutsläppare som annars får en orimlig inverkan på poängspektrat. Utan denna gräns får det stora flertalet kommuner mycket snarlika poäng, trots att det föreligger väsentliga skillnader i deras utsläpp. ")]),_c('h5',[_vm._v("Nyckeltal 2 – Utsläpp koldioxid totalt (0 – 2 poäng)")]),_c('p',[_vm._v("Data och poäng beräknas på samma sätt som för Utsläpp växthusgaset totalt (ovan), men tar enbart hänsyn till koldioxid (CO2), och inga andra växthusgaser.")]),_c('h5',[_vm._v("Nyckeltal 3 – Förändringstakt utsläpp totalt (−2 – 3 poäng)")]),_c('p',[_vm._v("Här bedöms hur utsläppen förändrats mellan mätperiodens första (2013) och sista (2017) år. Siffran som bedöms och rankas är den årliga förändringstakten (year-on-year) för hela mätperioden. Kommun med bäst resultat (störst utsläppsminkningstakt) får 3 poäng. Kommun med sämst resultat (störst utsläppsökningstakt) får -2 poäng.")]),_c('h5',[_vm._v("Nyckeltal 4 – Förändringstakt utsläpp koldioxid (−2 – 3 poäng)")]),_c('p',[_vm._v("Data och poäng beräknas på samma sätt som för ”Förändringstakt utsläpp totalt” (ovan), men tar enbart hänsyn till koldioxid (CO2), och inga andra växthusgaser. ")]),_c('h4',[_vm._v("Mer om poängsättningen av nyckeltal 3 och 4")]),_c('p',[_vm._v("Det stora flertalet kommuner minskar sina utsläpp, men det finns ett litet antal kommuner där utsläppen istället ökar. Därför talar vi om förändringstakt istället för minskningstakt. Här har vi konstaterat att det är orimligt att ökande utsläpp ger poäng. Därför har vi infört en möjlighet att få minuspoäng – något som bara ett fåtal kommuner drabbas av. ")]),_c('p',[_vm._v("Bland detta fåtal kommer finns några som avviker väldigt mycket från genomsnittskommunen genom att öka sina utsläpp avsevärt. Deras mycket avvikande förändringstakt innebär samtidigt att det samlade spektra vi har att ta hänsyn till (från den kommun som minskar sin utsläpp snabbast till den kommun som istället ökar dem snabbast) breddas. Om vi vid poängsättningen tillämpar en linjär exponent får därmed ett fåtal kommuner oproportionerligt stort inflytande på poängsättningen. I praktiken pressas då det stora flertalet kommuner samman och får likartade poäng. Här blir det relevant att koncentrera sig på flertalet och upprätta en poängexponent som faktiskt värderar de skillnader som föreligger mellan dessa. En s-formad exponent (poängkurva) är en konventionell metod för att ta denna typ av hänsyn och ranka denna typ av dataunderlag. S-kurvan kan sägas dra ut mittfältet.")]),_c('img',{attrs:{"src":require("../assets/s-kurva_bild.jpg"),"alt":"Bild som visar s-kurva"}}),_c('p',{staticClass:"image-description"},[_vm._v("Här syns den s-formade exponent (poängkurva) som vi tillämpat. De verkliga datapunkterna (kommunerna) för nyckeltalet Förändringstakt utsläpp totalt är inprickade i denna bild.")])])])}]

